import axios from 'axios';

import common from './commonUtil';

const api = {
    useFakeData: true,//json

    // getjson
    getJson(url, data) {
        return axios({
            method: "get",
            url: `./static/json/${url}.json`,
            data: data ? data : {}
        });
    },

    // get
    get(url, data) {
        //json
        if (common.isDev() || this.useFakeData) {
            return this.getJson(url, data);
        }

        return axios({
            method: "get",
            url: url,
            data: data ? data : {}
        });
    },

    // post
    post(url, data) {
        //json
        if (common.isDev() || this.useFakeData) {
            return this.getJson(url, data);
        }

        return axios({
            method: "post",
            url: url,
            data: data ? data : {}
        });

    }

}

export default api;



